/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

 .block-container{
	border: 1px dotted #f00;
	height: 100%;
	min-height: 200px;
	margin-left: 0;
	margin-right: 0;
	max-width: none;
}

.image-container {
	background: black;
	position: relative;
	display: flex;
    justify-content: center;
    align-items: center;
}

.color-container {
	height: 100vh;
	display: flex;
    justify-content: center;
    align-items: center;
}

.title-input {
	position:absolute;
	z-index: 100;
}

input[type="text"]{
	background: none;
    outline: none;
    border: none;
	text-align: center;
	font-size: 30px !important;
	color: white;
	&:focus {
		outline: none;
	}
}

.image-preview {
	opacity: 0.5;
}

.components-base-control__field {
	margin-bottom: 0 !important;
}